header{position:fixed;height:min-content;top:50px;width:100%;max-width:1440px;display:flex;justify-content:space-between;align-items:flex-end;padding:0 20px;z-index:7}header .logoContainer{display:flex;justify-content:center;align-items:center;gap:10px}header .logoContainer img{height:55px;width:55px}header .logoContainer .companyName{margin:0%;font-size:24px;font-weight:600;text-shadow:rgba(0,0,0,.5) 0px 0px 26px}header .logoContainer .companyName span{position:relative;font-size:22px}header .logoContainer .companyName span::after{content:".";position:absolute;top:-2.3px;right:-0.5px;color:#e6007e;font-size:30px;line-height:0%}header .headerLinks{position:relative;display:flex;align-items:center;justify-content:space-between;gap:30px}@media(max-width: 490px){header .headerLinks{gap:20px}}header .headerLinks a{text-decoration:none}header .headerLinks a span{position:absolute;top:20px;left:13px;text-align:center;border-radius:50%;background-color:#e6007e;width:20px;font-size:14px}header .headerLinks img[alt=меню]{cursor:pointer}